.book {
  height: 48rem;
  display: flex;
  color: #000;
  justify-content: space-between;
}

.book__photo {
  height: 100%;
  width: 50%;
}
.book__photo {
  -webkit-box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.75);
  box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.75);
  border-radius: 0.2rem;
  background-image: url('../../img/book-time.jpg');
  background-position: center;
  background-attachment: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.book__form .line-break--orange {
  margin-bottom: 1.2rem;
}

.book__form {
  height: 35rem;
  width: 56rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-right: 3rem;
}

.form__column {
  display: flex;

  width: 100%;
  height: 25%;
}

label {
  width: 50%;
  margin: 0.5rem;
}
input {
  width: 100%;
  height: 100%;
  background: #e5e5e5;
  border-radius: 0.5rem;
  outline: none;
  padding: 2rem;
  border: none;
}
input[type='date'],
input[type='time'] {
  color: #808080;
}
