@import url(https://fonts.googleapis.com/css2?family=Open+Sans&family=Poppins:wght@200;300;500&display=swap);
:root {
  --color-primary: #f7af2b;
  --color-primary-light-1: #fbda9e;
  --color-primary-light-2: #f9c86c;
  --color-primary-dark-1: #956206;
  --color-primary-dark-2: #432c03;
  --heading-font: 'Poppins', sans-serif;
  --body-font: 'Open Sans', sans-serif;

  --color-secondary: #d91e2a;
}
*,
*::after,
*::before {
  padding: 0;
  margin: 0;
  box-sizing: inherit;
}
html {
  font-size: 62.5%;
  /* 1rem=10px */
}

body {
  font-family: 'Open Sans', sans-serif;
  font-family: var(--body-font);
  font-size: 1.6rem;
  line-height: 1.7;
  font-weight: 300;
  color: #fff;
  box-sizing: border-box;
}

h1 {
  font-family: 'Poppins', sans-serif;
  font-family: var(--heading-font);
  font-weight: 300;
  font-weight: 400%;
  -webkit-animation: fadeDown 1s;
          animation: fadeDown 1s;
}
h2,
h3,
h4,
h5 {
  font-family: 'Poppins', sans-serif;
  font-family: var(--heading-font);
  font-weight: 300;
}

h2 {
  font-size: 300%;
}

h3 {
  font-size: 225%;
}
.line-break--orange {
  width: 13rem;
  height: 0.3px;
  border-radius: 100px;
  border-bottom: 3px solid #f7af2b;
  border-bottom: 3px solid var(--color-primary);
}
h4 {
  font-size: 150%;
}

h5 {
  font-size: 110%;
}

#footer {
  margin: 0;
  height: 10rem;
  width: 100%;
  background: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes fadeDown {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@keyframes fadeDown {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 1;
  }
}

@-webkit-keyframes fadeRight {
  0% {
    opacity: 0;
    transform: translateX(-25%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes fadeRight {
  0% {
    opacity: 0;
    transform: translateX(-25%);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@-webkit-keyframes fadeBottom {
  0% {
    opacity: 0;
    transform: translateY(25%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes fadeBottom {
  0% {
    opacity: 0;
    transform: translateY(25%);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.row {
  width: 95%;
  margin: 0 auto;
}

/* Styling Header */

section {
  display: flex;
  justify-content: center;
  margin-top: 8rem;
}

.navbar {
  height: 8rem;
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
}

.burger-toggler {
  display: none;
}
.navbar__items {
  display: flex;
  flex-direction: row;
  height: 100%;
  width: 100%;
  list-style: none;
  justify-content: space-between;
  align-items: center;
}
.navbar__item {
  position: relative;
  cursor: pointer;
  height: 100%;
  width: 100%;

  z-index: 1;
  font-family: var(--heading-font);
  font-weight: 400;
  letter-spacing: 1px;
}
.navbar__item > span {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
}
.navbar__item:hover {
  color: var(--color-primary-light-2);
  transform: translateY(-1px);
}
.navbar__item.active,
.navbar__item:active {
  color: var(--color-primary-light-2);

  transform: translateY(2px);
} /* hover effects onnavigation item  for pseudo class*/

.navbar__item::after {
  content: '';
  z-index: 5;
  width: 0%;
  height: 0.2rem;
  opacity: 0;
  position: absolute;
  background-color: var(--color-primary-light-2);
  bottom: 0;
  left: 0;
  transition: all 0.4s;
}

.navbar__item:hover:after {
  width: 100%;
  opacity: 1;
}
.navbar__item.active::after {
  opacity: 0;
}

.sticky {
  background: rgba(0, 0, 0, 0.63);
  height: 6rem;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  box-sizing: content-box;
  z-index: 9999;
}

.sticky .navbar__item:hover {
  color: var(--color-primary-light-2);
  transform: translateY(0);
}

@media only screen and (max-width: 600px) {
  .burger-toggler {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 100%;
    align-items: center;
  }
  .navbar__items {
    display: none;
  }
  .open {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 8rem;
    -webkit-animation: menuAppear 1s;
            animation: menuAppear 1s;
  }
  .navbar__item {
    padding-bottom: 0.5rem;
    background: rgba(0, 0, 0, 0.83);
  }
  .sticky .open {
    top: 6rem;
  }
  .navbar__logo {
    height: calc(100% - 1rem);
    width: 6rem;
    background-image: url(/food-court/static/media/logo.dc55914e.png);
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
  }
  .button-toggler {
    position: absolute;
    right: 0;
  }
  .menu-toggler {
    border: 1px grey solid;
    border-radius: 0.4rem;
    padding: 0.2rem;
  }
  .sticky .menu-toggler {
    margin-right: 2rem;
    align-self: center;
    fill: red;
  }
}

@-webkit-keyframes menuAppear {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

@keyframes menuAppear {
  0% {
    opacity: 0;
    transform: translateX(-100px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}

