  letter-spacing: 0;
  line-height: 1;
}

.header__heading--sub {
  font-size: 150%;
  letter-spacing: 0.1rem;
  align-self: right;
}
.header__heading--sub strong {
  color: var(--color-primary);
}

/* SETTING SIZE OF OPENGOUR AND SOCIAL LINKS */
