}
.header__openhours p {
  text-transform: uppercase;
  letter-spacing: 1px;
  font-size: 80%;
  font-weight: 400;
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  height: 100%;
  text-align: center;
  -webkit-text-orientation: mixed;
          text-orientation: mixed;
}

/* ICONS SOCIAL ON HEADER */

.header__social {
  display: flex;
  flex-direction: column;
  height: 100%;
  align-items: center;
  justify-content: center;
  border-left: 1px solid rgba(255, 255, 255, 0.2);
}

.social__icon--big {
  width: 5rem;
  height: 5rem;
  border: 1px solid rgba(255, 255, 255, 0.2);
  margin: 0.7rem;
  border-radius: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* Hover effects on social media SVG */

.header__logo {
  -webkit-animation: fadeDown 1s;
          animation: fadeDown 1s;
  width: 100%;
  height: 30%;
  background-image: url(/food-court/static/media/logo.dc55914e.png);
  background-repeat: no-repeat;
  background-position: top;
  background-size: contain;
}

.header__cta {
  display: flex;
  justify-content: center;
}

.btn {
  -webkit-touch-callout: none; /* iOS Safari */
  -webkit-user-select: none; /* Safari */ /* Konqueror HTML */
  -moz-user-select: none; /* Old versions of Firefox */
  -ms-user-select: none; /* Internet Explorer/Edge */
  user-select: none;

  transition: transform 0.2s;
  border-radius: 0.5rem;

  text-transform: uppercase;
  font-size: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem;
  cursor: pointer;
  font-family: var(--heading-font);
}
.btn--header {
  width: 20rem;
  height: 6rem;
  letter-spacing: 1px;
  font-weight: 400;
}
.btn:hover {
  transform: translateY(-1px);
}
.btn:active {
  transform: translateY(1px);
}

.btn__cta--main {
  position: relative;
  background-color: var(--color-primary);
  /* -webkit-box-shadow: 0px 0px 5px 0px var(--color-primary-light-2);
  -moz-box-shadow: 0px 0px 5px 0px var(--color-primary-light-2);
  box-shadow: 0px 0px 5px 0px var(--color-primary-light-2); */
  z-index: 0;
  transition: all 0.2s;
  -webkit-animation: fadeRight 1s;
          animation: fadeRight 1s;
}

.btn__cta--main:hover {
  color: var(--color-secondary);
}

.btn__cta--main::after {
  content: '';
  top: 0;
  left: 0;
  z-index: -1;

  background-color: var(--color-primary);
  z-index: 2;
  position: absolute;
}
.btn__cta--main:hover::after {
  -webkit-animation: expand 0.81s;
          animation: expand 0.81s;
}

.btn__cta--info {
  -webkit-animation: fadeBottom 0.5s;
          animation: fadeBottom 0.5s;
  border: 1px solid var(--color-secondary);
}

.header__swipe {
  display: flex;
  justify-content: center;
}

@-webkit-keyframes expand {
  0% {
    width: 0%;
    height: 0%;
    transform: translateX(2rem);
    opacity: 0;
  }
  10% {
    width: 2%;
    top: 0.5rem;

    height: 80%;
    transform: translateX(0);
    opacity: 0.8;
  }
  100% {
    width: 100%;
    height: 100%;
    opacity: 0.2;
  }
}

@keyframes expand {
  0% {
    width: 0%;
    height: 0%;
    transform: translateX(2rem);
    opacity: 0;
  }
  10% {
    width: 2%;
    top: 0.5rem;

    height: 80%;
    transform: translateX(0);
    opacity: 0.8;
  }
  100% {
    width: 100%;
    height: 100%;
    opacity: 0.2;
  }
}

